body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* In a CSS file or a style block in your component */
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  background-color: #01922f;
  color: white;
  padding: 10px;
}

header h1 {
  font-size: 2rem;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.upload-section {
  text-align: center;
  margin: 20px 0;
}

input[type="file"] {
  display: none;
}

button {
  padding: 10px 20px;
  background-color: #ff6600;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  margin-top: 10px;
}

.results-section {
  margin-top: 50px;
}

.grading-results {
  text-align: center;
  background-color: #f0f0f0;
  padding: 50px;
  border-radius: 10px;
  box-shadow: 0 8px 4px rgba(0, 0, 0, 0.1);
  box-sizing: inherit;
}

.upload-button{
  display: inline-block;
  padding: 10px 20px;
  background-color: #0074d9;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 5px;
  text-align: center;
}

.upload-button{
  background-color: #00a64b;
}

/* Add these CSS rules to style the table */
.grading-table {
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

h2 {
  font-size: 1.5rem;
  text-align: center;
}

table {

  margin-left: auto;
  margin-right: auto;
  width: 200%;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  padding: 10px;
  text-align: center;
}

th {
  background-color: #f0f0f0;
}

/* Add hover effect for table rows */
tr:hover {
  background-color: #f5f5f5;
}

.button-container {
  margin-right: center; /* Add margin-right to create space between buttons */
  display: flex;
  justify-content: space-between;
}

.button-container button {
  width: 200px;
  border-radius: 10px;
  margin-right: 100px; /* Add margin-right to create space between buttons */
}